:root {
  --color-primary: #cb1107;
  --color-primary: #db2a27;
  --color-primary-dark: #ac0e06;
  --color-primary-dark: #93140d;
  --color-primary-med: #b62821;
  --color-secondary: #f8ad3a;
  --color-secondary: #fdb916;
  --color-secondary-dark: #c57a07;
  --color-secondary-light: rgb(249.3125, 188.375, 94.9375);
  --color-green: #78a842;
  --color-white: #ffffff;
  --color-ivory: #fffcf4;
  --color-body-text: #000;
  --font-abel-regular: "Abel", "Arial Narrow", Arial, san-serif;
  --font-montserrat: "Montserrat", sans-serif;
  --font-hannah-reg: "ywft_hannahregular", sans-serif;
  --font-hannah-narrow: "ymft-hannahnarrow", "Arial Narrow", sans-serif;
  --font-hannah-wide: "ymft-hannahwide", sans-serif;
  --border-radius: 0.25rem;
  --font-komika-title: "komika_titleregular", "Georgia";
}

.justify-right {
  flex: 1;
  justify-content: flex-end;
}

.zigzag-border {
  padding-bottom: 32px !important;
}
.zigzag-border:after {
  background: linear-gradient(-45deg, #f9f7f3 16px, transparent 0), linear-gradient(45deg, #f9f7f3 16px, transparent 0);
  background-position: left-bottom;
  background-repeat: repeat-x;
  background-size: 32px 32px;
  content: " ";
  display: block;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 32px;
}

.dotted-border-bottom-secondary {
  border-bottom: 12px dotted var(--color-secondary);
  border-bottom: 12px dotted var(--color-secondary);
}

.color-red {
  color: var(--color-primary-med) !important;
}
.color-red p,
.color-red span,
.color-red li {
  color: var(--color-primary-med) !important;
}

.bg-primary {
  background-color: var(--color-primary) !important;
}

.color-white {
  color: white !important;
}

.bg-whitepaper {
  background-image: url("../imgs/whitepaper-tile4.jpg");
  background-repeat: repeat;
}

body {
  font-family: var(--font-montserrat);
  font-weight: 400;
  background-color: var(--color-white);
  color: var(--color-body-text);
}

a {
  color: var(--color-primary-med);
  text-decoration: none;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
  outline: 2px dotted var(--color-primary-med);
}

h1,
h2,
h3 {
  font-family: "Abel", sans-serif;
}

h1 {
  font-size: 2.625em;
  text-transform: uppercase;
  letter-spacing: 0.001em;
  color: var(--color-primary);
}
h1.page-title {
  color: var(--color-primary);
  margin-bottom: 1.625rem;
}
h1.woocommerce-products-header__title {
  border-bottom: 2px solid var(--color-primary-med);
}

h3 {
  color: var(--color-primary-dark);
}

button,
input[type=button],
input[type=reset],
input[type=submit],
.button,
.widget a.button {
  background-color: var(--color-secondary);
  color: var(--color-primary-dark);
  border-radius: var(--border-radius);
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover,
.button:hover,
.widget a.button:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
}

button.alt,
input[type=button].alt,
input[type=reset].alt,
input[type=submit].alt,
.button.alt,
.widget-area .widget a.button.alt {
  background-color: var(--color-secondary);
  color: var(--color-primary-dark);
  border-radius: var(--border-radius);
}
button.alt:hover,
input[type=button].alt:hover,
input[type=reset].alt:hover,
input[type=submit].alt:hover,
.button.alt:hover,
.widget-area .widget a.button.alt:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
}

.single-product div.product table.variations .label label {
  font-weight: 700;
}
.single-product div.product table.variations .label label:after {
  content: ":";
}

.single-product div.product table.variations select {
  appearance: none;
  border-radius: 0.35rem;
  padding: 0.35rem;
  border: 2px solid var(--color-primary);
  background: var(--color-primary);
  color: white;
  width: 100%;
  max-width: 100%;
}
.single-product div.product table.variations select option {
  background: white;
  color: var(--color-body-text);
}
.single-product div.product table.variations select option:focus {
  background: green;
}

.custom-select {
  position: relative;
  margin-top: 1rem;
}
.custom-select:after {
  content: "";
  position: absolute;
  top: 40%;
  cursor: pointer;
  right: 20px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-color: #fff transparent transparent transparent;
  pointer-events: none;
}

img {
  border-radius: 0;
}

