.arrow-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem auto;
  width: 100%;
}
.arrow-title h2 {
  color: var(--color-primary);
  padding: 0 0.5em;
  margin: 0;
  font-size: 2.1em;
  width: 40%;
  text-align: center;
}
.arrow-title .triangle {
  display: inline-flex;
  width: 0;
  height: 0;
  border-width: 0 30px 30px 0;
  border-color: transparent var(--color-secondary) transparent transparent;
  border-style: solid;
  transform: rotate(45deg);
}
.arrow-title .triangle.left {
  transform: rotate(225deg);
}
.arrow-title .arrow-line {
  height: 27.5px;
  background-color: var(--color-secondary);
  width: 32%;
  margin: 0;
}
.arrow-title .arrow-line.left {
  margin-right: -15px;
}
.arrow-title .arrow-line.right {
  margin-left: -15px;
}

.storefront-secondary-navigation.woocommerce-active .site-header .site-branding {
  width: auto;
  max-width: 150px;
  float: none;
  position: absolute;
  width: auto;
  top: -250%;
  left: 5.75%;
  background-color: var(--color-primary-med);
}
.storefront-secondary-navigation.woocommerce-active .site-header .site-branding:after, .storefront-secondary-navigation.woocommerce-active .site-header .site-branding:before {
  content: "";
  position: absolute;
  border-top: 30px solid var(--color-primary-med);
  height: 0;
  width: 0;
  top: 100%;
}
.storefront-secondary-navigation.woocommerce-active .site-header .site-branding:after {
  border-left: 85px solid transparent;
  right: 0;
}
.storefront-secondary-navigation.woocommerce-active .site-header .site-branding:before {
  border-right: 85px solid transparent;
  left: 0;
}
.storefront-secondary-navigation.woocommerce-active .site-header .site-branding .custom-logo-link {
  padding: 1rem 0.5rem;
}
@media screen and (max-width: 824px) {
  .storefront-secondary-navigation.woocommerce-active .site-header .site-branding {
    width: 17%;
  }
}

.top-bar {
  width: 100%;
  background-color: var(--color-primary);
}
.top-bar .col-full {
  justify-content: flex-end;
}
.top-bar #menu-social-menu.menu {
  flex-direction: row;
  margin: 0;
  padding: 0;
  margin-top: 0.5rem;
}
.top-bar #menu-social-menu .menu-item {
  margin: 0 0.275rem;
}

.site-footer {
  padding: 1rem 2rem;
}
.site-footer .widget {
  padding: 0;
  margin: 0;
}
.site-footer .footer-widgets {
  padding-top: 0 !important;
}
.site-footer .col-full {
  padding: 0.5rem 0;
}
.site-footer .col-full.footer {
  justify-content: center;
}
.site-footer .col-full.footer [class*=social-menu-container] {
  width: 100%;
  display: flex;
  justify-content: center;
}
.site-footer .col-full.footer [class*=social-menu-container] .menu {
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
}
.site-footer .col-full.footer [class*=social-menu-container] .menu li {
  margin: 0 0.25rem;
}
.site-footer .col-full.footer .widget_nav_menu {
  width: 70%;
}
.site-footer .col-full.footer .widget_nav_menu .menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.site-footer .col-full.footer .widget_nav_menu .menu li {
  width: 100%;
  text-align: center;
  position: relative;
}
.site-footer .col-full.footer .widget_nav_menu .menu li a {
  text-decoration: none;
}
.site-footer .col-full.footer .widget_nav_menu .menu li:after {
  content: "|";
  position: absolute;
  right: 0;
  opacity: 0.6;
}
.site-footer .col-full.footer .widget_nav_menu .menu li:last-child:after {
  content: "";
}
.site-footer .copyright {
  margin-top: 1.5rem;
  opacity: 0.8;
  font-size: 0.8em;
}
.site-footer .copyright .message {
  font-style: italic;
}

.product .woocommerce-tabs .panel {
  width: 100%;
}

.single-product .site-content .col-full {
  background-image: url("../imgs/whitepaper-tile4.jpg");
  background-repeat: repeat;
  display: block;
  padding-left: 2rem;
  padding-right: 2rem;
  max-width: calc(75em - 4rem);
}

.single-product div.product .product_meta {
  font-size: 0.875em;
  padding-top: 1em;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  display: none;
}

.woocommerce-error,
.woocommerce-info,
.woocommerce-message,
.woocommerce-noreviews,
p.no-comments {
  background-color: var(--color-green);
  padding: 0.5em 2em 0.5em 3.5em;
}
.woocommerce-error:before,
.woocommerce-info:before,
.woocommerce-message:before,
.woocommerce-noreviews:before,
p.no-comments:before {
