  left: 1em;
}

/* Tabs */
.woocommerce-tabs ul.tabs {
  display: flex;
  width: 100%;
  float: none;
  justify-content: flex-start;
}

.woocommerce-tabs ul.tabs li {
  display: inline-flex;
  align-items: center;
  width: auto;
  min-width: 33.33333%;
  padding-left: 1rem;
  font-family: var(--font-abel-regular);
  font-size: 1.625em;
}
.woocommerce-tabs ul.tabs li a {
  display: inline-block;
  color: var(--color-primary) !important;
}
.woocommerce-tabs ul.tabs li.hide-content::after {
  top: auto;
  transform: rotate(90deg);
  align-self: center;
  color: var(--color-primary);
}
.woocommerce-tabs ul.tabs li.show-content::after {
  top: auto;
  transform: rotate(-90deg);
  align-self: center;
  color: var(--color-primary);
}

.woocommerce-tabs .panel {
  float: none;
  width: 100%;
  padding-left: 1rem;
}

#datepicker {
