  opacity: 0;
  -webkit-transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.3s;
  -moz-transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.3s;
  transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.3s;
  width: 233px;
  left: auto;
  right: -1.618em;
  border-radius: 5px;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.2);
  -webkit-transform: translateY(2em);
  -moz-transform: translateY(2em);
  -ms-transform: translateY(2em);
  -o-transform: translateY(2em);
  transform: translateY(2em);
  z-index: 99;
}

.site-header .site-header-cart .widget_shopping_cart:before {
  content: "";
  display: block;
  position: absolute;
  top: -1.1em;
  right: 2.618em;
  border: 0.618em solid transparent;
}

.site-header .site-header-cart.active .widget_shopping_cart {
  visibility: visible;
  opacity: 1;
  left: auto;
  right: -1.618em;
  -webkit-transform: translateY(1em);
  -moz-transform: translateY(1em);
  -ms-transform: translateY(1em);
  -o-transform: translateY(1em);
  transform: translateY(1em);
}

/* End Header Cart */
/*Search*/
.storefront-2-3 .site-header .site-search .widget_product_search form label:before {
  font-family: "Font Awesome 5 Free";
  font-size: 1.25em;
  font-weight: 900;
  content: "\f002";
}

.woocommerce-active .site-header .site-search {
  position: relative;
  font-size: 1em;
  width: auto;
  /* border: 1px solid white; */
  /* width: 2rem; */
}

.site-header .site-search input[type=search] {
  position: absolute;
  top: 0;
  width: 233px;
  margin-right: 0.618em;
  padding: 0.618em;
  border-radius: var(--border-radius);
  -webkit-transform: translateX(1.618em);
  -moz-transform: translateX(1.618em);
  -ms-transform: translateX(1.618em);
  -o-transform: translateX(1.618em);
  transform: translateX(1.618em);
  -webkit-transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.5s;
  -moz-transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.5s;
  transition: all, cubic-bezier(0.795, -0.035, 0, 1), 0.5s;
  opacity: 0;
  width: 0;
  visibility: hidden;
  z-index: 999;
}

.site-header .site-search.active input[type=search] {
  opacity: 1;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  visibility: visible;
  width: 100%;
}

.site-header .site-search .widget_product_search form {
  width: 0;
}

.site-header .site-search.active .widget_product_search form {
  width: 180px;
}

.site-header .site-search .widget_product_search form:before {
  /* display: none; */
  color: white;
}

.site-search .widget_product_search form input[type=search],
.site-search .widget_product_search form input[type=text] {
  padding-left: 0.618em;
}

.widget_search form::before,
.site-search .widget_product_search form::before {
  left: auto;
  right: 0;
  top: 1.5rem;
}

.site-header .site-search .widget_product_search form label {
  clip: auto;
  position: static !important;
  display: block;
  width: 100%;
  height: 2.618em;
  text-indent: -9999px;
}

.site-header .site-search .widget_product_search form label:hover,
.site-header .site-search .widget_product_search form label:active,
.site-header .site-search .widget_product_search form label:focus {
  background-color: transparent;
  padding: 0;
  box-shadow: none;
  border-radius: 0;
  line-height: 1.618;
  cursor: pointer;
}

.site-header .site-search .widget_product_search form label:before {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  line-height: 1.618;
  text-align: center;
  text-indent: 0;
  font-size: 1.618em;
}

